		//
		countDownTimer() {
			this.countDown = setInterval(() => {
				if (this.seconds === 0 && this.minutes !== 0) {
					this.seconds = 59;
					this.minutes -= 1;
				} else if (this.minutes === 0 && this.seconds === 0) {
					this.$refs.popupPay.close();
					this.seconds = 0;
					clearInterval(this.countDown);
					this.countDown = null;
					this.minutes = 30;
					this.seconds = 0;
				} else {
					this.seconds -= 1;
				}
			}, 1000);
		},
		num(n) {
			return n < 10 ? '0' + n : '' + n;
		},
