					data: {
						// key: 'e54514186dfbafae58736cd342838baf',
						// sid: '92478',
						key: "4145121d3d1b5dcf41702960f3bd5adc",
						sid: '126730',
						center: latitude + ',' + longitude,
						radius: Radius * 1000
					},
					success: res => {
						this.horsemanCount = res.data.data.count;
						this.horsemanList = res.data.data.results;

						if (this.horseman == false) {
							this.$refs.popups.open();
						}
					}
				});

				this.$refs.popups.open();
			} else {
				uni.showToast({
					icon: 'none',
					title: ''
				});
			}
		},
		async payOrder() {
			uni.showLoading({
				title: '....'
			});
			this.payFlag = false
			if (this.circleShow === 0) {
				let res = await this.$fetch(this.$api.orderPay, { orderId: this.orderId, payType: this.circleShow }, 'POST', 'form');
				console.log(res)
				if (res.code == 0) {
					uni.hideLoading();

					uni.showToast({
						icon: 'success',
						title: res.msg
					});

					clearInterval(this.countDown);
					this.countDown = null;
					this.minutes = 30;
					this.seconds = 0;

					this.$refs.popupPay.close();
					
					uni.setStorageSync('userSelect',1)
					setTimeout(() => {
						// uni.redirectTo({
						// 	url:'./storage?car=' + this.car
						// })
						uni.setStorageSync('orderRefresh', true)
						uni.switchTab({
							url:'../order/order'
						})
					
					}, 1500);
				} else {
					uni.showToast({
						icon: 'none',
						title: res.msg
					});
					
				}
			}

			if (this.circleShow === 1) {
				let res = await this.$fetch(this.$api.orderPay, { orderId: this.orderId, payType: this.circleShow }, 'POST', 'form');
				uni.requestPayment({
					provider: 'alipay',
					orderInfo: res.msg, //
					success: res => {
						uni.hideLoading();

						uni.showToast({
							icon: 'success',
							title: ''
						});

						clearInterval(this.countDown);
						this.countDown = null;
						this.minutes = 30;
						this.seconds = 0;

						this.$refs.popupPay.close();
						uni.setStorageSync('userSelect',1)
						setTimeout(() => {
							uni.setStorageSync('orderRefresh', true)
							uni.switchTab({
								url:'../order/order'
							})
						
						}, 1500);
					},
					fail: async (err) => {
						uni.showToast({
							icon: 'none',
							title: ''
						});
						this.$refs.popupPay.close();
						
						let msg = await this.$fetch(this.$api.orderDetail,{id:this.orderId},"POST","Form")
						
						if (msg.code == 0) {
							setTimeout(() => {
								uni.setStorageSync('orderRefresh', true)
								uni.switchTab({
									url:'../order/order'
								})
								uni.navigateTo({
									url:'../orderDetail/orderDetail?orderDetail=' + JSON.stringify(msg.data) + "&currentIndex=" + 0
								})
							},1500)
						
						}
					}
				});
			}

			if (this.circleShow === 2) {
				let res = await this.$fetch(this.$api.orderPay, { orderId: this.orderId, payType: this.circleShow }, 'POST', 'form');

				uni.requestPayment({
					provider: 'wxpay',
					orderInfo: res.data, //
					success: res => {
						uni.hideLoading();
						uni.showToast({
							icon: 'success',
							title: ''
						});
						clearInterval(this.countDown);

						this.countDown = null;
						this.minutes = 30;
						this.seconds = 0;

						this.$refs.popupPay.close();
						uni.setStorageSync('userSelect',1) 
						setTimeout(() => {
							uni.setStorageSync('orderRefresh', true)
							uni.switchTab({
								url:'../order/order'
							})
						
						}, 1500);
					},
					fail: async(err) =>{
						uni.showToast({
							icon: 'none',
							title: ''
						});
						this.$refs.popupPay.close();
						let msg = await this.$fetch(this.$api.orderDetail,{id:this.orderId},"POST","Form")
						
						if (msg.code == 0) {
							setTimeout(() => {
								uni.setStorageSync('orderRefresh', true)
								uni.switchTab({
									url:'../order/order'
								})
								uni.navigateTo({
									url:'../orderDetail/orderDetail?orderDetail=' + JSON.stringify(msg.data) + "&currentIndex=" + 0
								})
							},1500)
						
						}
					}
				});
			}
		},
		//
		async initRichText() {
			let res = await this.$fetch(this.$api.articleType, { type: 5 }, 'Post', 'Form');

			if (res.code == 0) {
				this.RichText = res.data;
			}
		},
		goToRichText(id) {
			uni.navigateTo({
				url: '../rich-text/rich-text?id=' + id
			});
		},
		choiceHelpbuyCircle () {
			this.helpbuyCircle = !this.helpbuyCircle
		}
	}
};
